import { Group, Sprite, Texture } from "three";
import { GameVO } from "../../../vo/GameVO";
import { Linear, TweenMax, Sine } from "gsap";
import { destroySprite, getSpriteWithTexture } from "../../../utils/utils";

type SuitType = {body:Texture, headlight:Texture, bodylight:Texture, rush_body:Texture, rush_bodylight:Texture};

enum Status{
  idle,
  running,
  rush,
  stop,
}

export class Car extends Group{
  private _currStatus:number;
  private _prevStatus:number;

  private _body:Sprite;
  private _whell_front:Sprite;
  private _whell_rear:Sprite;
  private _headlight:Sprite;
  private _bodylight:Sprite;

  private _rush_body:Sprite;
  private _rush_bodylight:Sprite;
  private _rush_taillight:Sprite;

  private _currTweens:TweenMax[] = [];
  private _whellTwObj = {rotation:0};
  
  constructor(suit:SuitType){
    super();
    this.init(suit);
    // console.log(this);
  }

  private init(suit:SuitType){
    this._body = getSpriteWithTexture(suit.body);
    this._whell_front = GameVO.assetManager.getSprite("car_whell");
    this._whell_rear = GameVO.assetManager.getSprite("car_whell");
    this._headlight = getSpriteWithTexture(suit.headlight);
    this._bodylight = getSpriteWithTexture(suit.bodylight);
    
    this._rush_body = getSpriteWithTexture(suit.rush_body);
    this._rush_bodylight = getSpriteWithTexture(suit.rush_bodylight);
    this._rush_taillight = GameVO.assetManager.getSprite("carlight_red");

    this.add(this._body, this._whell_front, this._whell_rear, this._headlight, this._bodylight, this._rush_body, this._rush_bodylight, this._rush_taillight);

    this._whell_front.position.set(-66 ,-10, 0);
    this._whell_rear.position.set(70 ,-10, 0);
    this._headlight.position.set(-172 , 0, 0);
    this._bodylight.position.set(-48 , 0, 0);

    this.setStatus(Status.idle);

    this._whell_front.material.map = GameVO.assetManager.getTexture("car_whell");
  }

  suitHeadlight(texture:Texture){
    this._headlight.material.map = texture;
  }

  run(){
    if(this._currStatus == Status.running) return;
    this.setStatus(Status.running);
  }
  rush(){
    if(this._currStatus == Status.rush) return;
    this.setStatus(Status.rush);
  }
  stop(){
    if(this._currStatus == Status.stop) return;
    this.setStatus(Status.stop);
  }

  private setStatus(status:number){
    this._currStatus = status;
    switch(this._currStatus){
      case Status.idle:
        this.onIdle();
        break;
      case Status.running:
        this.onRunning();
        break;
      case Status.rush:
        this.onRush();
        break;
      case Status.stop:
        this.onStop();
        break;
    }
    this._prevStatus = this._currStatus;
  }
  
  private onIdle(){
    this._whell_front.visible = this._whell_rear.visible = this._rush_body.visible = this._rush_bodylight.visible = this._rush_taillight.visible = false;

    this.killCurrTweens();
  }

  private onRunning(){
    this._rush_body.visible = this._rush_bodylight.visible = this._rush_taillight.visible = false;
    this._whell_front.visible = this._whell_rear.visible = true;
    
    this.killCurrTweens();

    if(this._prevStatus == Status.rush)
    {
      const tw = TweenMax.to(this._whellTwObj, .5, {rotation:this._whellTwObj.rotation+360, ease:Linear.easeNone, repeat:-1, onUpdate:()=>{
        this._whell_front.material.rotation = this._whellTwObj.rotation*Math.PI/180;
        this._whell_rear.material.rotation = this._whellTwObj.rotation*Math.PI/180;
      }})
      this._currTweens.push(tw);
  
      let timeScaleObj = {scale:4};
      this._currTweens.push(TweenMax.to(timeScaleObj, .65, {scale:2, onUpdate:()=>{
        tw.timeScale(timeScaleObj.scale);
      }}));

      this._rush_body.visible = this._rush_bodylight.visible = this._rush_taillight.visible = true;
      this._whell_front.visible = this._whell_rear.visible = true;
      this._currTweens.push(
        TweenMax.to(this._rush_body.material, .1, {opacity:0, ease:Linear.easeNone}),
        TweenMax.to(this._rush_bodylight.material, .4, {opacity:0, ease:Linear.easeNone}),
        TweenMax.to(this._rush_taillight.material, .4, {opacity:0, ease:Linear.easeNone})
      );
    }
    else
    {
      const tw = TweenMax.to(this._whellTwObj, .5, {rotation:this._whellTwObj.rotation+360, ease:Linear.easeNone, repeat:-1, onUpdate:()=>{
        this._whell_front.material.rotation = this._whellTwObj.rotation*Math.PI/180;
        this._whell_rear.material.rotation = this._whellTwObj.rotation*Math.PI/180;
      }})
      this._currTweens.push(tw);
  
      let timeScaleObj = {scale:1};
      this._currTweens.push(TweenMax.to(timeScaleObj, 10, {scale:2, onUpdate:()=>{
        tw.timeScale(timeScaleObj.scale);
      }}));
    }
  }

  private onRush(){
    this._rush_body.visible = this._rush_bodylight.visible = this._rush_taillight.visible = true;
    this._whell_front.visible = this._whell_rear.visible = true;

    this.killCurrTweens();

    const tw = TweenMax.to(this._whellTwObj, .5, {rotation:this._whellTwObj.rotation+360, ease:Linear.easeNone, repeat:-1, onUpdate:()=>{
      this._whell_front.material.rotation = this._whellTwObj.rotation*Math.PI/180;
      this._whell_rear.material.rotation = this._whellTwObj.rotation*Math.PI/180;
    }})
    this._currTweens.push(tw);

    let timeScaleObj = {scale:2};
    this._currTweens.push(TweenMax.to(timeScaleObj, .3, {scale:4, onUpdate:()=>{
      tw.timeScale(timeScaleObj.scale);
    }}));

    const origin_rush_body_width = this._rush_body.material.map.image.width;
    this._rush_body.scale.x *= 55/100;
    this._currTweens.push(TweenMax.to(this._rush_body.scale, .3, {x:origin_rush_body_width, ease:Sine.easeIn}));
    this._rush_body.position.x = 16;
    this._currTweens.push(TweenMax.to(this._rush_body.position, .3, {x:112, ease:Sine.easeIn}));
    this._rush_body.material.opacity = 0;
    this._currTweens.push(TweenMax.to(this._rush_body.material, .3, {opacity:.8, ease:Sine.easeIn}));

    const origin_rush_bodylight_width = this._rush_bodylight.material.map.image.width;
    this._rush_bodylight.scale.x *= 10/100;
    this._currTweens.push(TweenMax.to(this._rush_bodylight.scale, .6, {x:origin_rush_bodylight_width*165/100, ease:Sine.easeIn}));
    this._rush_bodylight.position.x = -35;
    this._currTweens.push(TweenMax.to(this._rush_bodylight.position, .6, {x:166, ease:Sine.easeIn}));
    this._rush_bodylight.material.opacity = 1;
    
    const origin_rush_taillight_width = this._rush_taillight.material.map.image.width;
    this._rush_taillight.scale.x *= 18/100;
    this._currTweens.push(TweenMax.to(this._rush_taillight.scale, .6, {x:origin_rush_taillight_width*150/100, ease:Sine.easeIn}));
    this._rush_taillight.position.y = 18;
    this._rush_taillight.position.x = 88;
    this._currTweens.push(TweenMax.to(this._rush_taillight.position, .6, {x:232, ease:Sine.easeIn}));
    this._rush_taillight.material.opacity = 1;

    setTimeout(()=>{this.run()}, 600);
  }

  private onStop(){
    this._rush_body.visible = this._rush_bodylight.visible = this._rush_taillight.visible = false;
    this._whell_front.visible = this._whell_rear.visible = true;

    this.killCurrTweens();
  }
  
  private killCurrTweens(){
    this._currTweens.forEach((t)=>{
      t.kill();
    });
    this._currTweens.length = 0;
  }

  dispose(){
    this.killCurrTweens();
    this._currTweens = undefined;
    this._whellTwObj = undefined;

    destroySprite(this._body);
    this._body = undefined;
    destroySprite(this._whell_front);
    this._whell_front = undefined;
    destroySprite(this._whell_rear);
    this._whell_rear = undefined;
    destroySprite(this._headlight);
    this._headlight = undefined;
    destroySprite(this._bodylight);
    this._bodylight = undefined;
    destroySprite(this._rush_body);
    this._rush_body = undefined;
    destroySprite(this._rush_bodylight);
    this._rush_bodylight = undefined;
    destroySprite(this._rush_taillight);
    this._rush_taillight = undefined;
  }

}//class Car


