import * as THREE from "three";
import { Texture, TextureLoader, SpriteMaterial, Sprite } from "three";

export class AssetManager{
  private _textures:{[name:string]:Texture} = {};

  load(sourceVars:{name:string, url:string}[], onComplete:()=>any){
    let sourceNum = sourceVars.length;
    var textureLoader = new TextureLoader();
    const onLoaded = (name:string) => (texture:Texture) =>{
      texture.generateMipmaps = false;
      // texture.magFilter = THREE.LinearFilter;
      texture.minFilter = THREE.LinearFilter;
      this._textures[name] = texture;
      sourceNum--;
      if(sourceNum == 0) onComplete();
    }
    sourceVars.forEach((sourceVar)=>{
      textureLoader.load(sourceVar.url, onLoaded(sourceVar.name));
    });
  }

  dispose(){
    Object.keys(this._textures).forEach((name)=>{
      this._textures[name].dispose();
      delete this._textures[name];
    });
    this._textures = undefined;
  }

  getTexture(name:string){
    return this._textures[name];
  }

  getSprite(name:string){
    const material = new SpriteMaterial( { map: this.getTexture(name) } );
    const sprite = new Sprite( material );
    sprite.scale.set( material.map.image.width, material.map.image.height, 1 );
    return sprite;
  }

}//class AssetManager


