import { Group, Sprite } from "three";
import { GameVO } from "../../../vo/GameVO";
import { TweenMax } from "gsap";
import { destroySprite } from "../../../utils/utils";

export class Scenery extends Group{
  _sec:number;
  _parallax:number;
  _dx:number;
  _tween:TweenMax;
  _scene_01:Sprite;
  _scene_02:Sprite;
  _scene_03:Sprite;

  private _isDay = true;
  set isDay(value:boolean){
    this._isDay = value;
    if(this._isDay){
      this._scene_01.material.map = GameVO.assetManager.getTexture("scene_d01");
      this._scene_02.material.map = GameVO.assetManager.getTexture("scene_d02");
      this._scene_03.material.map = GameVO.assetManager.getTexture("scene_d03");
    }else{
      this._scene_01.material.map = GameVO.assetManager.getTexture("scene_n01");
      this._scene_02.material.map = GameVO.assetManager.getTexture("scene_n02");
      this._scene_03.material.map = GameVO.assetManager.getTexture("scene_n03");
    }
  }

  constructor(sec:number, parallax:number){
    super();
    this._sec = sec;
    this._parallax = parallax;
    this.init();
    // console.log(this);
  }

  init(){
    this._scene_01 = GameVO.assetManager.getSprite("scene_d01");
    this._scene_03 = GameVO.assetManager.getSprite("scene_d03");
    this._scene_02 = GameVO.assetManager.getSprite("scene_d02");
    this.add(this._scene_01);
    this.add(this._scene_03);
    this.add(this._scene_02);

    this._dx = this._scene_01.scale.x;
  }

  run(){
    const distance = this._dx * this._sec * this._parallax;
    const obj = {x:0}
    const map:Sprite[] = [];

    this._parallax
    this._sec+1

    const len = Math.ceil(distance * this._parallax);
    for(let i=0; i<len;i++){
      if(i%3 == 0) map.push(this._scene_02);
      else if(i%3 == 1) map.push(this._scene_03);
      else map.push(this._scene_01);
    }

    this._tween = TweenMax.to(obj, this._sec, {ease:"race" as any, x:distance,
      onUpdate:()=>{
        this._scene_01.visible = this._scene_02.visible = this._scene_03.visible = false;
        const exit = map[Math.floor(obj.x/this._dx)];
        exit.position.x = obj.x%this._dx;
        exit.visible = true;
        const enter = map[Math.floor(obj.x/this._dx)+1];
        if(enter){
          enter.position.x = obj.x%this._dx-this._dx;
          enter.visible = true;
        }
      },
      onComplete:()=>{
        console.log("!!!!");
      }
    });
  }

  dispose(){
    this._tween.kill();
    this._tween = undefined;

    destroySprite(this._scene_01);
    this._scene_01 = undefined;
    destroySprite(this._scene_02);
    this._scene_02 = undefined;
    destroySprite(this._scene_03);
    this._scene_03 = undefined;
  }

}//class Scenery


