import { Sprite, Texture, SpriteMaterial } from "three";

export function destroySprite(target:Sprite, disposeMaterial:boolean = true){
  if(target.parent) target.parent.remove(target);
  if(disposeMaterial){
    target.material.dispose();
    target.material = undefined;
  }
}

export function getSpriteWithTexture(texture:Texture = null, scale?:{x:number,y:number,z:number}){
  const material = new SpriteMaterial( { map: texture } );
  const sprite = new Sprite( material );
  if(scale){
    sprite.scale.set(scale.x, scale.y, scale.z);
  }else if(texture){
    sprite.scale.set(texture.image.width, texture.image.height, 1);
  }
  return sprite;
}


