import { Group, Sprite } from "three";
import { GameVO } from "../../../vo/GameVO";
import { TweenMax } from "gsap";
import { destroySprite } from "../../../utils/utils";

export class Road extends Group{
  _sec:number;
  _dx:number; 
  _tween:TweenMax;
  _road_01:Sprite;
  _road_02:Sprite;
  _road_03:Sprite;
  _road_04:Sprite;

  private _isDay = true;
  set isDay(value:boolean){
    this._isDay = value;
    if(this._isDay){
      this._road_01.material.map = GameVO.assetManager.getTexture("road_d01");
      this._road_02.material.map = GameVO.assetManager.getTexture("road_d02");
      this._road_03.material.map = GameVO.assetManager.getTexture("road_d03");
      this._road_04.material.map = GameVO.assetManager.getTexture("road_d04");
    }else{
      this._road_01.material.map = GameVO.assetManager.getTexture("road_n01");
      this._road_02.material.map = GameVO.assetManager.getTexture("road_n02");
      this._road_03.material.map = GameVO.assetManager.getTexture("road_n03");
      this._road_04.material.map = GameVO.assetManager.getTexture("road_n04");
    }
  }

  constructor(sec:number){
    super();
    this._sec = sec;
    this.init();
    // console.log(this);
  }

  init(){
    this._road_04 = GameVO.assetManager.getSprite("road_d04");
    this._road_03 = GameVO.assetManager.getSprite("road_d03");
    this._road_02 = GameVO.assetManager.getSprite("road_d02");
    this._road_01 = GameVO.assetManager.getSprite("road_d01");
    this.add(this._road_04);
    this.add(this._road_03);
    this.add(this._road_02);
    this.add(this._road_01);

    this._dx = this._road_01.scale.x;
  }

  run(){
    const distance = this._dx * this._sec;
    const obj = {x:0}
    const map:Sprite[] = [];
    map.push(this._road_01);
    for(let i=0; i<this._sec-1;i++){
      if(i%2)map.push(this._road_03);
      else map.push(this._road_02);
    }
    map.push(this._road_04);

    this._tween = TweenMax.to(obj, this._sec, {ease:"race" as any, x:distance,
      onUpdate:()=>{
        this._road_01.visible = this._road_02.visible = this._road_03.visible = this._road_04.visible = false;
        const exit = map[Math.floor(obj.x/this._dx)];
        exit.position.x = obj.x%this._dx;
        exit.visible = true;
        const enter = map[Math.floor(obj.x/this._dx)+1];
        if(enter) {
          enter.position.x = obj.x%this._dx-this._dx;
          enter.visible = true;
        }
      },
      onComplete:()=>{
        console.log("!!!!");
      }
    });
  }

  dispose(){
    this._tween.kill();
    this._tween = undefined;

    destroySprite(this._road_01);
    this._road_01 = undefined;
    destroySprite(this._road_02);
    this._road_02 = undefined;
    destroySprite(this._road_03);
    this._road_03 = undefined;
    destroySprite(this._road_04);
    this._road_04 = undefined;
  }

}//class Road


