import * as THREE from 'three';
import { Scene, WebGLRenderer, OrthographicCamera } from 'three';
import * as Stats from 'stats.js';
import { Stage } from './stage/Stage';
import { GameVO } from './vo/GameVO';

export class Game{
  private _width:number;
  private _height:number;
  private _fps:number;
  private _container:HTMLElement;

  private _fpsInterval:number;
  private _lastDrawTime:number=performance.now();

  private _stats:Stats;

  private _renderer:WebGLRenderer;
  private _cameraOrtho:OrthographicCamera;
  private _sceneOrtho:Scene;

  private _domElement:HTMLCanvasElement;
  get domElement():HTMLCanvasElement {
    return this._domElement;
  }

  constructor(width:number, height:number, fps:number, container:HTMLElement){
    this._width = GameVO.WIDTH;
    this._height = GameVO.HEIGHT;
    this._fps = GameVO.FPS;
    this._container = container;

    this._fpsInterval = 1000/this._fps;

    this._stats = new Stats();
    this._container.appendChild( this._stats.dom );
    this._stats.dom.style.left="428px";

    this.animate = this.animate.bind(this);
    this.onWindowResize = this.onWindowResize.bind(this);

    this.init();
    this.render();
    this.animate(performance.now());
  }

  init() {
    this._cameraOrtho = new OrthographicCamera( - this._width / 2, this._width / 2, this._height / 2, - this._height / 2, 1, 20 );
    this._cameraOrtho.position.z = 10;

    this._sceneOrtho = new Scene();
    this._sceneOrtho.add(new Stage);

    // renderer
    this._renderer = new WebGLRenderer();
    // this._renderer.setPixelRatio( window.devicePixelRatio );
    this._renderer.setSize( this._container.clientWidth, this._container.clientHeight );
    this._renderer.setAnimationLoop(this.animate);

    this._container.appendChild( this._renderer.domElement );

    this.onWindowResize();
    window.addEventListener( 'resize', this.onWindowResize, false );
  }

  onWindowResize() {
    this._cameraOrtho.left = - this._width / 2;
    this._cameraOrtho.right = this._width / 2;
    this._cameraOrtho.top = this._height / 2;
    this._cameraOrtho.bottom = - this._height / 2;
    this._cameraOrtho.updateProjectionMatrix();

    this._renderer.setSize( this._container.clientWidth, this._container.clientHeight );
  }

  animate(timestamp:number) {

    // calc elapsed time since last loop

    var now = timestamp;
    var  elapsed = now - this._lastDrawTime;

    // if enough time has elapsed, draw the next frame
    
    if (elapsed > this._fpsInterval) {

        // Get ready for next frame by setting then=now, but also adjust for your
        // specified fpsInterval not being a multiple of RAF's interval (16.7ms)
        this._lastDrawTime = now - (elapsed % this._fpsInterval);

        // Put your drawing code here

        this._stats.begin();
        // monitored code goes here
        this.render();
        this._stats.end();
    }
  }

  render() {
    this._renderer.clear();
    //
    // this._renderer.clearDepth();
    this._renderer.render( this._sceneOrtho, this._cameraOrtho );
  }

}//class Game


