import { Group } from "three";
import { AssetManager } from "../utils/AssetManager";
import { sourceVars } from "../vo/sourceVars";
import { GameVO } from "../vo/GameVO";
import { UI } from "./ui/UI";
import { Race } from "./race/Race";

export class Stage extends Group{
  private _ui:UI;
  private _race:Race;

  constructor(){
    super();

    this.init();
  }

  init(){
    this.createAssetManager();
    
  }

  createAssetManager(){
    var am = new AssetManager();
    am.load(sourceVars, ()=>{
      GameVO.assetManager = am;
      this.onEnetr();
    });
  }

  onEnetr(){
    this._race = new Race();
    this.add(this._race);

    this._ui = new UI();
    this.add(this._ui);
  }




}//class Stage


