import { Group, Sprite } from "three";
import { GameVO } from "../../vo/GameVO";

export class UI extends Group{
  private _topUI:Sprite;
  private _bottomUI:Sprite;
  constructor(){
    super();

    this.init();
  }

  init(){
    this._topUI = GameVO.assetManager.getSprite("bg_top");
    this._topUI.position.set(0, GameVO.HEIGHT/2 - this._topUI.scale.y/2, 0);
    this.add(this._topUI);

    this._bottomUI = GameVO.assetManager.getSprite("bg_bottom");
    this._bottomUI.position.set(0, -GameVO.HEIGHT/2 + this._topUI.scale.y/2, 0);
    this.add(this._bottomUI);
  }

  dispsoe(){
    
  }

}//class UI


