import sound_on from './audio/sound_on.png';
import sound_off from './audio/sound_off.png';
import car01wav from './audio/car01.wav';

import { PureComponent } from 'react';
import * as React from 'react';
import * as ReactDOM from 'react-dom';
import { Game } from './three/Game';
// import * as Howl from 'howl';
import {Howl} from 'howler';
import styled from 'styled-components';

export default class ThreeGame extends PureComponent {
  _threeContainerElement:HTMLDivElement;
  _soundPlayed = false;
  _sound:Howl = new Howl({
    src: [car01wav]
  });
  render () {
    return (
      <div ref={element => this._threeContainerElement = element}
        style ={{
          width:"100%",
          height:"100%",
        }}
      >
        {/* <button 
          style={{

          }}
          onClick={()=>{
            if(this._sound.volume()==0){
              this._sound.volume(1);
            }else{
              this._sound.volume(0);
            }
          }}
        /> */}
      </div>
    );
  }
  componentDidMount() {
    console.log(car01wav);
    // const game = new Game(1000, 652, 30, this._threeContainerElement);

    // this._sound  = new Howl({
    //   src: [car01wav]
    // });
    // this._sound.play();

    var sound = new Howl({
      src: [car01wav],
      sprite: {
        race: [0, 20000],
      }
    });
    
    // Clear listener after first call.
    sound.once('load', ()=>{
      new Game(1000, 652, 30, this._threeContainerElement);
      setTimeout(()=>{
        sound.play('race');
      }, 1000);
    });
  }
}//class ThreeGame

ReactDOM.render(
  <div style ={{
    // width:"50vw",
    // height:"50vh",
    // width:`${1000}px`,
    // height:`${652}px`,
    width:`${1000/2}px`,
    height:`${652/2}px`,
  }}>
    <ThreeGame/>
    <ThreeGame/>
  </div>,
  document.getElementById('root')
);


