import { Car } from "./Car";
import { TweenMax, Linear } from "gsap";

type SetRange = {min:number, max:number};

export class CarMove{
  // _maxForwardSpeed = 200;
  // _maxBackwardSpeed = 80;
  // _rushSpeed = 120;
  _maxForwardSpeed = 300;
  _maxBackwardSpeed = 200;
  _rushSpeed = 140;

  _rushGapSpeed = 60;
  _dislikeMoveDx = 10;

  _cars:Car[];

  constructor(cars:Car[]){
    this._cars = cars;

    this.setPosition();
  }

  setPosition() {
    // this._cars[0].scale.set(.72, .72, 1);
    // this._cars[0].position.set(456, 112, 0);
    // this._cars[9].scale.set(1, 1, 1);
    // this._cars[9].position.set(305, -210, 0);
    
    let ds = (1-.72)/9;
    let dx = (305-456)/9;
    let dy = (-210-112)/9;

    for(let i=0; i<10; i++){
      this._cars[i].scale.set(.72+ds*i, .72+ds*i, 1);
      this._cars[i].position.set(456+dx*i, 112+dy*i*(.72+ds*i), 0);
      if(i>0 && i<9){
        this._cars[i].position.x += 4;
        this._cars[i].position.y += i*i/8;
      }
    }
  }
  
  run(ranks:number[]){
    this._cars.forEach((car, ind)=>{
      this.runCar(car, ind, ranks[ind]);
    });
  }

  runCar(car:Car, ind:number, rank:number){
    let leftTime = 20;
    const startX = car.position.x;
    const endX = startX - 640;
    const targetX = endX + (rank-1)*(startX-endX)/10;
    
    let beginning = true;
    const move = () => {
      let runTime = Math.floor(Math.random()*4+2);
      let afterTime = leftTime - runTime;
      if(afterTime<0){
        runTime = leftTime;
        afterTime = 0;
      }
      leftTime = afterTime;
      
      const currX = car.position.x;
      let tweenX;
      if(afterTime > 0)
      {
        const possibleLeft = currX-runTime*this._maxForwardSpeed;
        const possibleRight = currX+runTime*this._maxBackwardSpeed;
        const allowLeft = targetX-afterTime*this._maxForwardSpeed;
        const allowRight = targetX+afterTime*this._maxBackwardSpeed;
        const tweenRange = beginning?[startX-250, startX]:[Math.max(possibleLeft, allowLeft, endX-100), Math.min(possibleRight, allowRight, startX)];
        tweenX = tweenRange[0] + Math.random()*(tweenRange[1]-tweenRange[0]);
      }
      else
      {
        tweenX = targetX;
      }

      if((currX-tweenX)/runTime > this._rushSpeed){
        car.rush();
      }else{
        car.run();
      }

      TweenMax.to(car.position, runTime, {x:tweenX, ease:Linear.easeOut,
        onComplete:()=>{
        if(leftTime>0) move();
        else car.stop();
      }});

      beginning = false;
    }
    move();
  }

  rangeUnionSet(...ranges:SetRange[]):SetRange{
    if(ranges.length==1){
      return ranges[0];
    }if(ranges.length==2){
      const r1 = ranges[0];
      const r2 = ranges[1];
      const a = r1.min;
      const b = r1.max;
      const c = r2.min;
      const d = r2.max;
      if(c>b || a>d){
        return null;
      }else if(a>=c&&b<=d){
        return r1;
      }else if(c>=a&&d<=b){
        return r2;
      }else if(c<=a&&d>=a&&d<=b){
        return {min:a, max:d};
      }else if(a<=c&&b>=c&&b<=d){
        return {min:c, max:b};
      }else{
        return null;
      }
    }else{
      const rs = ranges.concat();
      let us = this.rangeUnionSet(rs.shift(), rs.shift());
      while(rs.length>0){
        if(!us) return us;
        us = this.rangeUnionSet(us, rs.shift());
      }
      return us;
    }
  }

  rangeDiffSet(range: SetRange, diffRanges: SetRange[]): SetRange[]{
    return null;
  }

  dispose(){

  }

}//class CarMove


